body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
	font-family: "mrDafoe";
	src: url(/static/media/MrDafoe-Regular.93322e09.ttf);
}
.lead
{
  text-align: center;
}


.logo
{
  word-spacing: 10px;
  letter-spacing: 1px;
  font-family: "mrDafoe","Times New Roman", serif;
}
.jumbotron {
  min-height: 200px;
  padding-top: 50px;
  background: #f7f7f7;
}

.jumbotron a
{  color:rgb(50, 104, 145) !important;}

